import Vue from  'vue'

Vue.filter('orderState', function(str) {
	switch(str){
		case 'WAIT_BUYER_PAY':
        	return '' 
        	break;
		case 'WAIT_SELLER_CONFIRM':
        	return '' 
        	break;
		case 'SELLER_CONSIGNED_PART':
        	return '' 
        	break;
		case 'WAIT_SELLER_SEND_GOODS':
        	return '' 
        	break;
		case ' WAIT_BUYER_CONFIRM_GOODS':
        	return '' 
        	break;
		case 'TRADE_BUYER_SIGNED':
        	return '' 
        	break;
		case 'TRADE_FINISHED':
        	return '' 
        	break;
		case 'TRADE_CLOSED':
        	return '' 
        	break;
		case 'TRADE_CLOSED_BY_UDUI':
        	return '' 
        	break;
        default : 
        	return '';
	}
});

Vue.filter('date', function(str) {
   let  myTime = {
            /**              
             *               
             * @param <int> unixTime    ()              
             * @param <bool> isFull    (Y-m-d  Y-m-d H:i:s)              
             * @param <int>  timeZone                 
             */
            UnixToDate: function(unixTime, isFull, timeZone) {
                if (typeof (timeZone) == 'number')
                {
                    unixTime = parseInt(unixTime) + parseInt(timeZone) * 60 * 60;
                }
                var time = new Date(unixTime);
                var ymdhis = "";
                ymdhis += time.getFullYear() + "-";
                ymdhis += (time.getMonth()+1) + "-";
                ymdhis += time.getDate() + " ";

                
                ymdhis += " " + time.getHours() + ":";
                ymdhis += time.getMinutes() + ":";
                ymdhis += time.getSeconds();
                
                return ymdhis;
            }
        }
        return myTime.UnixToDate(str)
});


